@import "../common/styles/sass/variables.scss";
@import "../common/styles/sass/mixins";

.mainBlock {
    height: 100vh;
    background-color: $bgColor;
    padding: 20px 0;
    @media screen and (max-height: 680px){
            height: auto;
    }
    @media screen and (max-width: 1077px){
        height: auto;
        padding: 100px 0;
    }
    .particles {
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        opacity: 0.3;
    }
    .mainContainer {
        @include container();
        justify-content: space-around;
        .text {
            h1 {
                @include textMainBlock(('Montserrat', sans-serif), 36px, 40px, 700, normal, uppercase, 0 0 15px);
            }
            h5 {
                @include textMainBlock(('Montserrat', sans-serif), 16px, 1.1, 700, 1px, uppercase, 0 0 15px);
            }
            h3 {
                @include textMainBlock(('Montserrat', sans-serif), 24px, 1.2, 700, 1px, uppercase, 0 0 15px);
            }
        }
        .photo {
            width: 400px;
            height: 500px;
            background-size: cover;
            background-position: center;
            background-repeat: no-repeat;
            @media screen and (max-width: 450px){
                height: 450px;
                width: 350px;
            }
            @media screen and (max-width: 400px){
                height: 400px;
                width: 300px;
            }
        }
    }
}










